.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.mainWrap {
  display: grid;
  grid-template-columns: 230px 1fr;
  height: 100vh;
}

.aside{
  background-color: #c0c0c0;
  display: flex;
  flex-direction: column;
}

.asideInner{
  flex: 1;
  width: 200px;
  margin: auto;
  align-items: center;
  text-align: center;
}

.clientsList{
  /* margin-left: 40px; */
  border: 1px solid black;
}
.btn{
  outline: none;
  height: 35px;
  margin-top: 10px;
}
.realtimeEditorDiv{
  /* margin: 20px; */
}
#realtimeEditor{
  padding: 20px;
  border: none;
  outline: none;
}

.homePageWrapper{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.formwrapper{
  padding: 20px;
  border: 1px solid black;
  border-radius: 10px;
  width: 400px;
  max-width: 90%;
}

.inputGroup{
  display: flex;
  flex-direction: column;
}

.inputBox{
  padding: 10px;
  /* border-radius: 5px;
  border: none;
